.card {
  all: unset;
  background-color: #fff;
  border-radius: 10px;
  -webkit-box-shadow: 4px 4px 25px rgba(0, 0, 0, 0.05);
          box-shadow: 4px 4px 25px rgba(0, 0, 0, 0.05);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 300px;
  padding-bottom: 10px;
  position: relative;
}

.card .status {
  all: unset;
  position: absolute;
  top: 20px;
  right: 20px;
  color: #ffc01e;
  font-weight: 700;
  font-size: 0.9em;
  background-color: #fff3d2;
  padding: 5px 15px;
  line-height: 1em;
  border-radius: 50px;
}

.card .card_image_container {
  width: 100%;
}

.card .card_image_container img {
  width: 100%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.card .card_talk {
  padding: 10px 30px;
}

.card .card_talk .title_container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.card .card_talk .title_container button {
  all: unset;
  cursor: pointer;
  background-color: #009153;
  padding: 5px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 3px;
}

.card .card_talk .progress {
  margin-top: 10px;
  margin-bottom: 15px;
}

.card .card_talk .progress progress {
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  height: 8px;
  width: 100%;
  position: relative;
}

.card .card_talk .progress progress::-webkit-progress-bar {
  background-color: #e5fff4;
  border: 1px solid #009153;
  border-radius: 100px;
}

.card .card_talk .progress progress::-webkit-progress-value {
  position: absolute;
  top: 0;
  background-color: #009153;
  border: none;
  border-radius: 100px;
}

.card .card_talk .progress .progress_status {
  font-size: 0.7em;
  font-weight: 600;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 50%;
  left: 50%;
}

.card .card_talk :global(.text-primary) {
  font-weight: 600;
  font-size: 1.2em;
  color: #00422e;
  line-height: 1em;
}

.card .card_talk img {
  margin-top: 10px;
  margin-bottom: 15px;
}

.card .card_talk .label {
  font-size: 1em;
  font-weight: 600;
}

.card .card_talk .btn_group {
  margin-top: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  gap: 10px;
}

.card .card_talk .btn_group .btn {
  all: unset;
  padding: 1px 8px;
  border-radius: 50px;
  font-size: 0.8em;
  text-align: center;
  min-width: 40px;
}

@media screen and (max-width: 400px) {
  .card .card_talk .btn_group :global(.btn) {
    padding: 1px 10px;
    font-size: 0.8em;
  }
}

@media screen and (max-width: 800px) {
  .card .card_talk :global(.text-primary) {
    font-size: 1.1em;
  }
}
